:import('../../../colors.css') {
  lightestShadow: lightestShadow;
  blue: blue;
  red: red;
  darkGrey: darkGrey;
}

.Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 2px;
  -webkit-box-shadow: lightestShadow;
          box-shadow: lightestShadow;
  height: auto;
  margin-bottom: 10px;
}

.Avatar {
  margin: 10px 20px;
  margin-top: 15px;
  /* width: 100px; */
}

.Avatar span {
  margin-right: 10px;
}

.Notification {
  background: red;
  color: white;
  padding: 3px 10px;
  border-radius: 10px;
}

.Row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  /* width: 150px; */
}

.Row button {
  position: relative;
  right: -30px;
}

.Role {
  margin: 20px;
  color: darkGrey;
}

.Edit {
  /* padding: 5px; */
  margin-right: 20px;
  cursor: pointer;
  font-size: 19px;
  color: black;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  color: #888;
}

.Edit:hover {
  color: darkGrey;
}

.Trash {
  font-size: 22px;
  color: red;
  margin-right: 20px;
  cursor: pointer;
}

.Trash:hover {
  color: #888;
}

.DropDown {
  /* width: 100px; */
  margin-right: 20px;
}

@media screen and (max-width: 800px) {
  .Trash, .Edit {
    /* justify-content: flex-start; */
    margin-right: 5px;
  }
  .Row {

    /* width: 100px; */
  }
}


