:import("../../../colors.css") {
  lightBlue: gridBlue;
  blue: blue;
}

.Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  margin-top: 10px;
  font-size: 18px;
  width: 100%;
}

.Input {
  /* width: 100%; */
  padding: 10px 0;
  border: none;
  /* border-radius: 4px; */
  border-bottom: 2px solid blue !important;
  background: none !important;
  font-size: 20px;
  caret-color: blue;
  color: #222;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  font-family: inherit;
}

.Input:-webkit-autofill {
  -webkit-text-fill-color: lightBlue !important;
}

.Input:focus {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  border: none !important;
  outline: none !important;
  border-bottom: 2px solid lightBlue !important;
}

.Label {
  margin: 10px 0;
  text-align: left;
  font-size: 15px;
  text-transform: uppercase;
  color: #666;
}



