.List {
  width: 100%;
  border: 1px solid #ddd;
  height: 100%;
  overflow-y: scroll;
}

.ListItem {
  background: #f9f9f9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-align: left;
  width: 100%;
  height: 30px;
}

.Odd {
  background: #999;
}

.ErrorText {
  margin-top: 20px;
  -ms-flex-item-align: center;
      align-self: center;
  font-style: italic;
  color: #777;
}


