:import('../../colors.css') {
  blue: blue;
  green: green;
  blueHover: blueHover;
  gray: backgroundGray;
  background: mainBackground;
}

.RoomDescription {
  /* padding: 30px; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  -ms-flex-order: 2;
      order: 2;
  height: 100%;
  padding: 5px 10px 0px 10px;
}

.TabNameTitle {
  border-bottom: 1px solid #ddd;
  height: 25px;
  font-size: 18px;
  font-weight: 700;
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
}

.ToggleView {
  float: right;
  position: relative;
  top: -22px;
  cursor: pointer;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.ToggleView:hover {
  background: #ddd;
}

.InstructionsContainer {
  margin-top: 10px;
  padding-left: 5px;
  height: 100%;
  padding-bottom: 15px;
  -ms-flex: 1 0 1;
      flex: 1 0 1;
}

.Collapsed {
  display: none;
  border: none;
}

.InstructionsTitle {
  float: left;
  margin-right: 10px;
}

.CopyIcon {
  color: #999;
  cursor: pointer;
  margin-left: 3px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.CopyIcon:hover {
  color: blueHover;
}

.CopiedContainer {position: relative;}

.Copied {
  position: absolute;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  color: green;
}


