@value colors: '../../colors.css';
@value blue from colors;
:import('../../colors.css'){
  background: mainBackground;
}

.Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  height: 100%;
  background-color: white;
  overflow-y: hidden;
  border-radius: 3px;
}

.Avatar {
  margin: 10px 0;
  -webkit-transition: .25s;
  -o-transition: .25s;
  transition: .25s;
}

.Active {
  font-size: 16px;
  font-weight: 700;
}

.Passive {
  font-size: 14px;
}

.Expanded {
  -ms-flex-item-align: start;
      align-self: flex-start;
  width: 100%;
  overflow-y: auto;
  padding: 5px 10px;
  height: 100%;
  max-height: 220px;
}

.Collapsed {
  display: none;
}


.Expanded::-webkit-scrollbar {
  width: 0.5em;
  height: 0.09em;
  background-color: scrollBackground;
}

.Expanded::-webkit-scrollbar-thumb {
  background-color: scrollBar;
  border-radius: 15px;
}

.Title {
  padding: 8px 10px 8px 10px;
  border-bottom: 1px solid #ddd;
  background-color: rgb(245, 245, 245);
  border-radius: 3px 3px 0 0;
  color: black;
  font-size: 17px;
  letter-spacing: 1px;
  cursor: pointer;
}

.Count {
  color: darkGrey;
  float: right;
  position: relative;
  right: 0px;
  top: 0px;
}


