:import("../../colors.css") {
  gridBlue: gridBlue;
  darkBlue: darkBlue;
}

@-webkit-keyframes move_wave {
  0% {
    -webkit-transform: translateX(0) translateZ(0) scaleY(1);
            transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    -webkit-transform: translateX(-25%) translateZ(0) scaleY(0.65);
            transform: translateX(-25%) translateZ(0) scaleY(0.65);
  }
  100% {
    -webkit-transform: translateX(-50%) translateZ(0) scaleY(1);
            transform: translateX(-50%) translateZ(0) scaleY(1);
  }
}

@keyframes move_wave {
  0% {
    -webkit-transform: translateX(0) translateZ(0) scaleY(1);
            transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    -webkit-transform: translateX(-25%) translateZ(0) scaleY(0.65);
            transform: translateX(-25%) translateZ(0) scaleY(0.65);
  }
  100% {
    -webkit-transform: translateX(-50%) translateZ(0) scaleY(1);
            transform: translateX(-50%) translateZ(0) scaleY(1);
  }
}

@-webkit-keyframes move_wave_2 {
  0% {
    -webkit-transform: translateX(0) translateZ(0) scaleY(1);
            transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    -webkit-transform: translateX(-25%) translateZ(0) scaleY(0.25);
            transform: translateX(-25%) translateZ(0) scaleY(0.25);
  }
  100% {
    -webkit-transform: translateX(-50%) translateZ(0) scaleY(1);
            transform: translateX(-50%) translateZ(0) scaleY(1);
  }
}

@keyframes move_wave_2 {
  0% {
    -webkit-transform: translateX(0) translateZ(0) scaleY(1);
            transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    -webkit-transform: translateX(-25%) translateZ(0) scaleY(0.25);
            transform: translateX(-25%) translateZ(0) scaleY(0.25);
  }
  100% {
    -webkit-transform: translateX(-50%) translateZ(0) scaleY(1);
            transform: translateX(-50%) translateZ(0) scaleY(1);
  }
}

.Container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
}

.backgroundGraph {
  position: absolute;
  top: 50vh;
  /* bottom: 35%; */
  left: 0;
  right: 0;
  /* overflow-x: hidden; */
  background-size: 25px 25px;
  background-image: -webkit-linear-gradient(left, gridBlue 0.5px, transparent 1px),
    -webkit-linear-gradient(top, gridBlue 0.5px, transparent 1px) !important;
  background-image: -o-linear-gradient(left, gridBlue 0.5px, transparent 1px),
    -o-linear-gradient(top, gridBlue 0.5px, transparent 1px) !important;
  background-image: linear-gradient(to right, gridBlue 0.5px, transparent 1px),
    linear-gradient(to bottom, gridBlue 0.5px, transparent 1px) !important;
  font-family: "Voces", "sans-serif";
  z-index: 20;
  height: 20vh;
  border-bottom: 1px solid #d9d9d9;
}
.waveWrapper {
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 40%;
  top: 0;
  margin: auto;
}
.waveWrapperInner {
  position: absolute;
  width: 100%;
  overflow: hidden;
  height: 100%;
  bottom: -1px;
  background-image: -webkit-gradient(
    linear,
    left bottom, left top,
    color-stop(1%, #d4edfc),
    color-stop(15%, gridBlue),
    to(darkBlue)
  );
  background-image: -webkit-linear-gradient(
    bottom,
    #d4edfc 1%,
    gridBlue 15%,
    darkBlue 100%
  );
  background-image: -o-linear-gradient(
    bottom,
    #d4edfc 1%,
    gridBlue 15%,
    darkBlue 100%
  );
  background-image: linear-gradient(
    to top,
    #d4edfc 1%,
    gridBlue 15%,
    darkBlue 100%
  );
}
.bgTop {
  z-index: 15;
  opacity: 0.7;
}
.bgMiddle {
  z-index: 16;
  opacity: 0.7;
}
.bgBottom {
  z-index: 17;
  opacity: 0.4;
}
.wave {
  position: absolute;
  left: 0;
  width: 200%;
  height: 100%;
  background-repeat: repeat no-repeat;
  background-position: 0 bottom;
  -webkit-transform-origin: center bottom;
      -ms-transform-origin: center bottom;
          transform-origin: center bottom;
}
.waveTop {
  background-size: 50% 100px;
  background-image: url("./wave-top.png");
}
.waveAnimation .bgTop {
  -webkit-animation: move-wave 3s linear infinite;
          animation: move-wave 3s linear infinite;
  /* -webkit-animation: move-wave 3s;
  -webkit-animation-delay: 1s;
  animation-delay: 1s; */
}
.waveMiddle {
  background-size: 50% 120px;
  background-image: url("./wave-mid.png");
}
.waveAnimation .waveMiddle {
  -webkit-animation: move_wave 10s linear infinite;
          animation: move_wave 10s linear infinite;
}
.waveBottom {
  background-size: 50% 100px;
  background-image: url("./wave-bot.png");
}
.waveAnimation .waveBottom {
  -webkit-animation: move_wave 15s linear infinite;
          animation: move_wave 15s linear infinite;
}



