:import("../../colors.css") {
  graphHeight: graphHeight;
  graphWidth: graphWidth;
  lightShadow: lightShadow;
  red: red;
}

.Graph {
  height: 100%;
  width: 100%;
  position: absolute;
  /* border: 1px solid #ddd; */
}

.ControlWarning {
  position: fixed;
  background: #efefef;
  color: red;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  z-index: 601;
  padding: 10px;
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
  border-radius: 4px;
}
/* ^^ THIS WILL ONLY SHOW UP IF THEY'VE HACKED THE JAVASCRIPT */

.ReferenceLine {
  position: absolute;
  height: 10px;
  width: 10px;
  background: red;
  z-index: 5000;
}

.ImportOption {
  height: 110px;
}

.ImportOption input {
  width: 100%;
}

.Info {
  margin: 15px 0;
}



