:import("../../../colors.css") {
  blue: blue;
  lightBlue: lightBlue;
  orange: orange;
  gridBlue: gridBlue;
  darkOrange: darkOrange;
  red: red;
  lightRed: lightRed;
  lightGrey: lightGrey;
  darkGrey: darkGrey;
  lightestShadow: lightestShadow;
  lightShadow: lightShadow;
  newBoxShadow: newBoxShadow;
}

.Button {
  /* background-color: white; */
  border: none;
  -ms-flex-item-align: center;
      align-self: center;
  padding: 8px;
  border-radius: 3px;
  /* border: 1px solid #d3d2d4; */
  height: 100%;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  font-size: inherit;
  letter-spacing: 0.08em;
  cursor: pointer;
  -webkit-box-shadow: lightestShadow;
          box-shadow: lightestShadow;
  transition: 0.2s;
}

.Big {
  composes: Button;
  color: white;
  padding: 20px 30px;
  background: none;
  border: 2px solid white;
  border-radius: 3px;
  font-family: inherit;
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-transition: 0s;
  -o-transition: 0s;
  transition: 0s;
  /* box-shadow: 0; */
}

.Big:hover {
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
  border: 2px solid lightBlue;
  border-bottom: 2px solid blue;
  background-image: -webkit-gradient(linear, left top, left bottom, from(lightBlue), color-stop(90%, blue));
  background-image: -webkit-linear-gradient(top, lightBlue, blue 90%);
  background-image: -o-linear-gradient(top, lightBlue, blue 90%);
  background-image: linear-gradient(-180deg, lightBlue, blue 90%);
}

.Big:active {
  background-image: none;
  background-color: #3796f5;
}

.Small {
  composes: Button;
  background-color: blue;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#91c4ff), to(#2a85e0));
  background-image: -webkit-linear-gradient(top, #91c4ff, #2a85e0 100%);
  background-image: -o-linear-gradient(top, #91c4ff, #2a85e0 100%);
  background-image: linear-gradient(-180deg, #91c4ff, #2a85e0 100%);
  color: white;
  padding: 10px 20px;
  font-family: inherit;
  text-transform: uppercase;
  font-size: 14px;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.Small:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#8dbbf8), to(#2a85e0));
  background-image: -webkit-linear-gradient(top, #8dbbf8, #2a85e0 100%);
  background-image: -o-linear-gradient(top, #8dbbf8, #2a85e0 100%);
  background-image: linear-gradient(-180deg, #8dbbf8, #2a85e0 100%);
}

.Small:active {
  background-image: none;
  background-color: #3796f5;
}

.Cancel {
  composes: Button;
  background-color: #999;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  color: white;
  padding: 10px 20px;
  font-family: inherit;
  text-transform: uppercase;
  font-size: 14px;
}

.xs {
  composes: Button;
  padding: 2px;
  background-color: #999;
  color: white;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.edit {
  composes: Button;
  padding: 8px 12px;
  margin: 0 10px;
  font-size: 14px;
  letter-spacing: 0.02em;
  background-color: #ccc;
}

.edit-save {
  composes: Button;
  padding: 8px 12px;
  margin: 0 10px;
  font-size: 14px;
  letter-spacing: 0.02em;
  background-color: blue;
  color: white;
}

.xs:hover {
  background-color: blue;
}

.Cancel {
  composes: Button;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ddcfcf), color-stop(90%, lightGrey));
  background-image: -webkit-linear-gradient(top, #ddcfcf, lightGrey 90%);
  background-image: -o-linear-gradient(top, #ddcfcf, lightGrey 90%);
  background-image: linear-gradient(-180deg, #ddcfcf, lightGrey 90%);
  color: white;
  text-transform: uppercase;
  font-weight: 700;
}

.SmallCancel:hover {
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
}

/* .Small:hover {
  box-shadow: lightShadow;
} */

.Cancel:hover {
  /* box-shadow: lightShadow; */
  background-image: -webkit-gradient(linear, left top, left bottom, from(#cfc2c2), color-stop(90%, lightGrey));
  background-image: -webkit-linear-gradient(top, #cfc2c2, lightGrey 90%);
  background-image: -o-linear-gradient(top, #cfc2c2, lightGrey 90%);
  background-image: linear-gradient(-180deg, #cfc2c2, lightGrey 90%);
}

.Cancel:active {
  background: #999;
}

.Primary {
  composes: Button;
}

.Secondary {
  composes: Button;
  color: white;
  background-color: darkOrange;
}

.Active {
  composes: Button;
  color: white;
  background-color: blue;
}

.Primary:hover {
  /* color: white; */
  background-color: #f9f9f9;
  border: 1px solid #babbb2;
}

.Active:hover {
  color: white;
  background-color: blue;
}

.Secondary:hover {
  color: white;
  -webkit-box-shadow: newBoxShadow;
          box-shadow: newBoxShadow;
}

.Disabled {
  composes: Small;
  color: #333;
  cursor: not-allowed;
  background: -webkit-gradient(linear, left bottom, left top, from(#b9b0b0), color-stop(90%, lightGrey));
  background: -webkit-linear-gradient(bottom, #b9b0b0, lightGrey 90%);
  background: -o-linear-gradient(bottom, #b9b0b0, lightGrey 90%);
  background: linear-gradient(to top, #b9b0b0, lightGrey 90%);
}

.Disabled:hover {
  background: -webkit-gradient(linear, left bottom, left top, from(#b9b0b0), color-stop(90%, lightGrey));
  background: -webkit-linear-gradient(bottom, #b9b0b0, lightGrey 90%);
  background: -o-linear-gradient(bottom, #b9b0b0, lightGrey 90%);
  background: linear-gradient(to top, #b9b0b0, lightGrey 90%);
}

.Danger {
  composes: Button;
  color: white;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fa8d8d), color-stop(90%, red));
  background-image: -webkit-linear-gradient(#fa8d8d, red 90%);
  background-image: -o-linear-gradient(#fa8d8d, red 90%);
  background-image: linear-gradient(#fa8d8d, red 90%);
}

.Danger:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f37878), color-stop(90%, red));
  background-image: -webkit-linear-gradient(#f37878, red 90%);
  background-image: -o-linear-gradient(#f37878, red 90%);
  background-image: linear-gradient(#f37878, red 90%);
}

.Danger:active {
  background: #ec4545;
}



