.Loading {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #f8f8f8;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Graph {
  height: 100px;
  width: 100px;
  /* overflow-x: hidden; */
  border: 2px solid inset #f8f8f8;
  background-size: 11px 11px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-image: -webkit-linear-gradient(left, black 0.35px, transparent 1px),
    -webkit-linear-gradient(top, black 0.35px, transparent 1px) !important;
  background-image: -o-linear-gradient(left, black 0.35px, transparent 1px),
    -o-linear-gradient(top, black 0.35px, transparent 1px) !important;
  background-image: linear-gradient(to right, black 0.35px, transparent 1px),
    linear-gradient(to bottom, black 0.35px, transparent 1px) !important;
}



