}

.GgbIcon:hover {
  cursor: pointer;
  background: -webkit-gradient(linear, left bottom, left top, from(#d3d0d3), to(#b2b3b4));
  background: -webkit-linear-gradient(bottom, #d3d0d3, #b2b3b4);
  background: -o-linear-gradient(bottom, #d3d0d3, #b2b3b4);
  background: linear-gradient(to top, #d3d0d3, #b2b3b4);
