:import("../../colors.css") {
  blue: blue;
  darkShadow: darkShadow;
  navbarHeight: navbarHeight;
}

.NavContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: navbarHeight;
  background-color: blue;
  color: white;
  z-index: 350;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.Fixed {
  composes: NavContainer;
  position: fixed;
  height: navbarHeight;
}

.WorkspaceNav {
  composes: NavContainer;
  -webkit-box-shadow: none;
          box-shadow: none;
  background: transparent;
}

.LogoContainer {
  -ms-flex-order: 1;
      order: 1;
  -ms-flex: 0.25 0 auto;
      flex: 0.25 0 auto;
}

.Logo {
  height: 100%;
  margin-left: 8%;
  -ms-flex-align: center;
      align-items: center;
  font-size: 25px;
  font-weight: 400;
}

.LogoShort {
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  font-size: 25px;
  font-weight: 700;
  display: none;
}

.NavListContainer {
  -ms-flex-order: 2;
      order: 2;
  -ms-flex: 0.75 0 auto;
      flex: 0.75 0 auto;
}

.NavListContainer ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-flow: center;
      flex-flow: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-line-pack: end;
      align-content: flex-end;
  -ms-flex-align: end;
      align-items: flex-end;
}

.NavListContainer ul li {
  -ms-flex: 0.1 0 auto;
      flex: 0.1 0 auto;
}

@media screen and (min-width: 1300px) {
  .NavContainer {
    border-radius: 0px;
  }
}

@media screen and (max-width: 620px) {
  .LogoShort {
    display: block;
    margin-left: 25px;
  }
  .Logo {
    display: none;
  }
}



