:import("../../../colors.css") {
  blue: blue;
  lightBlue: lightBlue;
  yellow: yellow;
  orange: orange;
  teal: teal;
  red: red;
  lightShadow: lightShadow;
  backgroundGray: backgroundGray;
  lightGrey: lightGrey;
}

.BreadcrumbContainer {
  z-index: 500;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  /* background-color: #f9f9f9; */
  margin-top: 20px;
  padding: 10px 0;
  border-radius: 3px;
  border-bottom: 1px solid #ddd;
}

.CrumbContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-item-align: center;
      align-self: center;
  text-align: left;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: "-";
     text-overflow: "-";
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.Crumb {
  position: relative;
  color: lightGrey;
  -ms-flex: 0.25 0 auto;
      flex: 0.25 0 auto;
  padding: 0 20px;
  text-align: left;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: "-";
     text-overflow: "-";
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.Seperator {
  width: 10px;
  -ms-flex: 0.1 0 auto;
      flex: 0.1 0 auto;
  color: lightGrey;
}

.Hidden {
  display: none;
}

.Active {
  color: black;
  font-weight: 600;
  /* border: 2px solid yellow; */
}

.Ntf {
  background-color: red;
  font-size: 0.68em;
  color: white;
  border-radius: 50px;
  padding: 2px;
  position: absolute;
  top: 0px;
  right: 6px;
}



