:import("../../../colors.css") {
  darkBlue: darkBlue;
  blue: blue;
  darkShadow: darkShadow;
  gridBlue: gridBlue;
  lightShadow: lightShadow;
  navbarHeight: navbarHeight;
}

.Nav {
  width: 100%;
  height: navbarHeight;
  /* background-image: linear-gradient(to top, #3a5ec9 10%, #001144 100%); */
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 1000;
  border-top: 2px solid darkBlue;
  position: fixed;
  color: white;
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-transition: color 1s, background 0.5s, -webkit-box-shadow 0.5s;
  transition: color 1s, background 0.5s, -webkit-box-shadow 0.5s;
  -o-transition: color 1s, background 0.5s, box-shadow 0.5s;
  transition: color 1s, background 0.5s, box-shadow 0.5s;
  transition: color 1s, background 0.5s, box-shadow 0.5s, -webkit-box-shadow 0.5s;
}

.LightNav {
  color: blue;
  border-top: 0px;
  background: rgb(255, 255, 255);
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.LightNav li a {
  color: blue;
}

.GradientNav {
  /* this important tag only overrides the background: none in .Nav */
  /* background-image: linear-gradient(to top, blue %, darkBlue 100%) !important;
   */
  background: -webkit-gradient(linear, left top, left bottom, from(#001144), to(#0e2257));
  background: -webkit-linear-gradient(top, #001144, #0e2257);
  background: -o-linear-gradient(top, #001144, #0e2257);
  background: linear-gradient(to bottom, #001144, #0e2257);
  -webkit-box-shadow: 0px 5px 20px 15px #0e2257;
          box-shadow: 0px 5px 20px 15px #0e2257;
}

.NavContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  max-width: 1700px;
  margin: 0 auto;
  /* border: 1px solid white; */
}

.LogoContainer {
  -ms-flex-order: 1;
      order: 1;
  -ms-flex: 0.2 0 auto;
      flex: 0.2 0 auto;
  /* border: 1px solid yellow; */
}

.Logo {
  font-size: 30px;
  height: 100%;
  margin-left: 8%;
  /* align-items: center; */
  font-weight: 400;
}

.LogoShort {
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  font-size: 25px;
  font-weight: 700;
  display: none;
}

.NavListContainer {
  -ms-flex-order: 2;
      order: 2;
  -ms-flex: 0.8 0 auto;
      flex: 0.8 0 auto;
  /* border: 1px solid red; */
}

.NavListContainer ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-flow: center;
      flex-flow: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-line-pack: end;
      align-content: flex-end;
  -ms-flex-align: end;
      align-items: flex-end;
  padding: 0;
}

.NavListContainer ul li {
  -ms-flex: 0.1 0 auto;
      flex: 0.1 0 auto;
}

@media screen and (max-width: 775px) {
  .LogoShort {
    display: block;
    margin-left: 25px;
  }
  .Logo {
    display: none;
  }
}



